@import url(https://fonts.googleapis.com/css2?family=Texturina:wght@100&display=swap);
.sidebar__container {
    position: fixed;
    width: 75%;
    left: -100%;
    z-index: 5;
    height: 100%;
    background-color: #fe5722;
    transition: all 0.3s ease-in-out;
}

.sidebar__items {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid ;

}

.sidebar__item {
    font-size: 1.2rem;
    color: white;

    
    padding: 0.5rem;
    text-align: center;
    cursor: pointer;
    border-top: 1px solid #854217;
}

.sidebar__item:hover {
    background-color: #854217;
}

.sidebar__toggle {
    font-size: 1.6rem;
    color: white;
    padding: 1rem;
    padding-left: 20%;
    cursor: pointer;
}

.sidebar__toggle:hover {
    background-color: #854217;
}


.active {
    left: 0%;
}
.header__container {
    background-color: #fe5722;
    position: fixed;
    width: 100%;
    z-index: 4;
}

.header__container__list {
    display: flex;
    align-items: center;
    justify-content: space-around;
    list-style: none;
}

.header__container__list img {
    max-width: 7rem;
}

.favourite-tastes-slide-bar__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 18px;
    background-color: white;
    padding-top: 120px;
}

.favourite-tastes-slide-bar__container > h2 {
    padding: 0.5em;
}

.favourite-tastes-slide-bar__container__slidebar {
    display: flex;
    justify-content: center;
    position: relative;
}

.favourite-tastes-slide-bar__container img {

    width: 100%;
    max-height: 5.5rem;
}

.category__container {
    padding: 0.2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}   


.category__container img {
    max-width: 170px;
    height: 220px;
    min-width: 100%;
    border-radius: 15px;
    -webkit-filter: brightness(55%);
            filter: brightness(55%);
}

.category__container p {
    position: relative;
    max-width: 160px ;
    max-height: 0px;   
    top: -150px;
    font-weight: bold;
    font-size: 1.5rem;

    color:white;
    text-align: center; 
}

.categories__container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
}

.categories__container h1 {
    padding: 10px;
    width: 100%;
    text-align: center;
}
.footer__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

.footer__container div {
    
    padding: 0.2rem;
    font-size: 2.5rem;
}

.footer__container div a  {
    color: black;
}


.footer__container p {
    padding: 0.3rem;
    font-size: 1.5rem;
}

#footer__designer-signature {
    padding-top: 0.2rem;
    font-size: 13px;
    color: grey;
}



.foods-page__content {
    padding-top: 100px;
}

.foods-page__content p {
    text-align: center;

    font-size: 1.5rem;
}


.foods-page__content div {
    text-align: center;


}


.food__container {
    padding: 0.2rem;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
}

.food__container img {
    max-width: 170px;
    height: 250px;
    min-width: 100%;
    border-radius: 15px;
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.food__container p {
    font-size: 1.15rem;
    text-align: center;
    align-self: center;
    font-weight: bold;
    color:black;
}

.food__table {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.food__table > div {
    padding: 0.2rem;
}


.foods__container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
}

.foods__container h1 {
    font-weight: bold;
    padding: 10px;
    width: 100%;
    text-align: center;
}


.food-page__content {
    padding-top: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.food-page__content img {
    border-radius: 1rem;
    max-width: 100%;
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.food-page__name > p {
    font-size: 2rem;

}

.food-page__table {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 20px;
    font-size: 20px;
}

.food-page__table p {

    font-size: 25px;
}

.food-page__des > p {
    font-size: 1.2rem;
    text-align: center;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Texturina', serif;
    font-weight: bold;
}

